}

:root {
  --text-color-primary: #efe5e5;
  --text-color-accent: #e17127;
  --text-color-dark: #433535;
  --background-color: #001e5a;
  --header-background-color: #001e5a;
  --header-color-primary: #efe5e5;
  --header-nav-color-accent: #e17127;
  --header-sub-menu-bcg-color: rgba(16, 52, 126, .792);
  --footer-background-color: #001e5a;
  --footer-border-vertic: 4px solid #5257ee;
  --footer-border-goriz: 1px solid #928484;
  --footer-socials-icon-fill: rgba(0, 30, 90, .96);
  --footer-socials-icon-bcg-color: #eeee08;
  --footer-socials-icon-fill-accent: #e17127;
  --footer-copyright-text-color: #928484;
  --animation-base: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 68px;
}

:before, :after {
  box-sizing: border-box;
}

body {
  letter-spacing: .04em;
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizespeed;
  height: 100%;
  color: var(--text-color-primary);
  background-color: var(--background-color);
  font-family: Montserrat, Roboto, Verdana, sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
  overflow-x: hidden;
}

body._lock {
  overflow: hidden;
}

body, h1, h2, h3, h4, p, ul, ol, li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
}

a, a:focus, a:hover, a:active, a:visited {
  color: inherit;
  text-decoration: none;
}

.link {
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
