.header {
  width: 100%;
  background-color: var(--header-background-color);
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
}

.header__container {
  min-height: 70px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  width: 50px;
  height: 50px;
  z-index: 5;
  position: relative;
}

.menu__icon {
  width: 30px;
  height: 18px;
  cursor: pointer;
  z-index: 5;
  display: block;
  position: relative;
}

@media screen and (min-width: 768px) {
  .menu__icon {
    display: none;
  }
}

@media screen and (max-width: 767px) {
  .menu__icon span, .menu__icon:before, .menu__icon:after {
    height: 10%;
    width: 100%;
    transition: all var(--animation-base);
    background-color: var(--text-color-primary);
    position: absolute;
    left: 0;
  }
}

@media screen and (max-width: 767px) {
  .menu__icon:before, .menu__icon:after {
    content: "";
  }
}

@media screen and (max-width: 767px) {
  .menu__icon:before {
    top: 0;
  }
}

@media screen and (max-width: 767px) {
  .menu__icon:after {
    bottom: 0;
  }
}

@media screen and (max-width: 767px) {
  .menu__icon span {
    top: 50%;
    transform: scale(1)translate(0, -50%);
  }
}

@media screen and (max-width: 767px) {
  .menu__icon._active span {
    transform: scale(0)translate(0, -50%);
  }
}

@media screen and (max-width: 767px) {
  .menu__icon._active:before {
    top: 50%;
    transform: rotate(-45deg)translate(0, -50%);
  }
}

@media screen and (max-width: 767px) {
  .menu__icon._active:after {
    bottom: 50%;
    transform: rotate(45deg)translate(0, 50%);
  }
}

@media screen and (max-width: 767px) {
  .menu__body {
    width: 100%;
    height: 100%;
    transition: left var(--animation-base);
    background-color: rgba(0, 0, 0, .9);
    padding: 100px 30px 30px;
    position: fixed;
    top: 0;
    left: -100%;
    overflow: auto;
  }
}

@media screen and (max-width: 767px) {
  .menu__body._active {
    left: 0;
  }
}

@media screen and (max-width: 767px) {
  .menu__body:before {
    content: "";
    width: 100%;
    height: 70px;
    background-color: var(--header-background-color);
    z-index: 2;
    position: fixed;
    top: 0;
    left: 0;
  }
}

.menu__list {
  flex-direction: column;
  align-items: center;
  gap: 50px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .menu__list {
    flex-direction: row;
    justify-content: space-around;
    gap: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .menu__list {
    gap: 55px;
  }
}

.menu__item {
  align-items: center;
  display: flex;
  position: relative;
}

@media screen and (max-width: 767px) {
  .menu__item {
    flex-wrap: wrap;
  }
}

.menu__link {
  text-align: center;
  color: var(--header-color-primary);
  flex: auto;
  font-family: Montserrat, Roboto, Verdana, sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.22222;
}

.menu__link:hover, .menu__link:focus {
  color: var(--header-nav-color-accent);
  transition: color .25s;
}

.menu__link:hover:before {
  transition: transform var(--animation-base);
  transform: scaleX(1);
}

.menu__link:before {
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--header-nav-color-accent);
  position: absolute;
  bottom: -5px;
  left: 0;
  transform: scaleX(0);
}

@media screen and (min-width: 768px) {
  .menu__link {
    font-size: 13px;
  }
}

@media screen and (min-width: 1200px) {
  .menu__link {
    font-size: 18px;
  }
}

.menu__arrow {
  width: 0;
  height: 0;
  transition: transform var(--animation-base);
  border-top: 10px solid #fff;
  border-left: 5px solid rgba(0, 0, 0, 0);
  border-right: 5px solid rgba(0, 0, 0, 0);
  margin: 0 0 0 5px;
  display: block;
}

.menu__item._active .menu__arrow {
  transform: rotate(-180deg);
}

.menu__link {
  transition: var(--animation-base);
}

.donat {
  border: 1px solid rgba(0, 0, 0, .765);
  border-radius: 10px;
  padding: 10px;
  box-shadow: 0 2px 4px rgba(14, 30, 37, .12), 0 2px 16px rgba(14, 30, 37, .32);
}

.menu__item:nth-child(2):hover .menu__arrow, .menu__item:nth-child(2):focus .menu__arrow {
  border-top: 10px solid var(--text-color-accent);
}

.menu__item:nth-child(2):hover .menu__link, .menu__item:nth-child(2):focus .menu__link {
  color: var(--header-nav-color-accent);
  transition: color .25s;
}

.menu__sub-list {
